import React, { useEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import { Form, Button } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import FormContainer from "../components/FormContainer";
import CheckoutSteps from "../components/CheckoutSteps";
import { saveShippingAddress } from "../actions/cartActions"

function ShippingScreen() {
    const navigate = useNavigate();
    const cart = useSelector(state => state.cart)
    const { shippingAddressFromStorage } = cart

    const dispatch = useDispatch()

    const [address, setAddress] = useState(shippingAddressFromStorage.address);
    const [city, setCity] = useState(shippingAddressFromStorage.city);
    const [postalCode, setPostalCode] = useState(shippingAddressFromStorage.postalCode);
    const [country, setCountry] = useState(shippingAddressFromStorage.country);

    const submitHandler = (e) => {
      e.preventDefault()
      console.log('Submitted')
      dispatch(saveShippingAddress({address, city, postalCode, country}))
      navigate('/payment')
    }

    return (
        <FormContainer>
            <CheckoutSteps step1 step2/>
            <h1 className="text-center">Shipping</h1>
            <Form onSubmit={submitHandler}>
                <Form.Group controlId="address">
                    <Form.Label>Address</Form.Label>
                    <Form.Control
                        required
                        type="text"
                        placeholder="Enter Address"
                        value={address ? address : ""}
                        onChange={(e) => setAddress(e.target.value)}
                    ></Form.Control>
                </Form.Group>

                <Form.Group controlId="city">
                    <Form.Label>City</Form.Label>
                    <Form.Control
                        required
                        type="text"
                        placeholder="Enter City"
                        value={city ? city : ""}
                        onChange={(e) => setCity(e.target.value)}
                    ></Form.Control>
                </Form.Group>

                <Form.Group controlId="postalCode">
                    <Form.Label>Zip Code</Form.Label>
                    <Form.Control
                        required
                        type="text"
                        placeholder="Enter Zip Code"
                        value={postalCode ? postalCode : ""}
                        onChange={(e) => setPostalCode(e.target.value)}
                    ></Form.Control>
                </Form.Group>

                <Form.Group controlId="country">
                    <Form.Label>Country</Form.Label>
                    <Form.Control
                        required
                        type="text"
                        placeholder="Enter Country"
                        value={country ? country : ""}
                        onChange={(e) => setCountry(e.target.value)}
                    ></Form.Control>
                </Form.Group>

                <Button type="submit" variant="outline-warning">
                  Continue
                </Button>
            </Form>
        </FormContainer>
    );
}

export default ShippingScreen;
